<template>
  <div>
    <div style="text-align:left">
    <b style="text-align:left;color:#767783">基础配置</b>
    </div>
    <hr align="left" style="border:3 double #987cb9" width="80%" color=#E3C9D8 SIZE=2>
    <div style="text-align:center">
    <!-- <input type="button" @click="gets()" value="点我异步获取数据(Get)" /> -->
    <!-- <span id="p1"> {{status}}</span> -->
    <el-form :rules="rules">
      <el-form-item label="服务器IP:">
    <el-transfer
    style="text-align: left; display: inline-block"
    filterable
    filter-placeholder="请输入ip"
    v-model="value"
    :titles="['未选中列表','选中列表']"
    :data="data">
  </el-transfer>
  </el-form-item>
  </el-form>
  <!-- <p v-for="item in value" :key="item">
    {{item}}
  </p> -->
  </div>
  <div style="text-align:left">
    <b style="text-align:left;color:#767783">连接网址</b>
    </div>
    <hr align="left" style="border:3 double #987cb9" width="80%" color=#E3C9D8 SIZE=2>
  <div class="demo-input-suffix">   
  <el-form ref='form' :model="form" label-width="80px" label-position='left'>
  <el-form-item label="测试域名" style="text-align:left">
   <el-input v-model="input" placeholder="例如：www.baidu.com" style="width: 30%"></el-input>
</el-form-item>
  </el-form>
  </div>


<el-row>
  <el-button type="success" @click="posts()" >提交</el-button>
</el-row>
  </div>
</template>

<style>
.switch .el-switch__label {
    position: absolute;
    color: #fff !important;
    z-index: 1;
    display: none;
}
.switch .is-active{
    display: block;
}
.switch .el-switch__label--left span{
    margin-left: 20px;
}
</style>

<script>
// var p1 = document.getElementById("p1");
import axios from "axios";
import {cron} from 'vue-cron'
// import {cronComponent} from 'vue-cron';
export default {
  data() {
      return {
        //穿梭框使用的值
        data: [],
        value: [],
        rules: {},
        input: '',
        form:{},
      };
   
  },
  created(){//页面渲染之前执行，调用定义的方法
                //3.调用定义的方法
                this.gets()
            },
  methods: {
    changeCron(val){
            this.execTime = val
        },
    gets: function () {
      var parms = "";
      axios
        .get("http://127.0.0.1:8092/getaxios", parms)
        .then((response) => {
          // window.alert(response.data);
          this.data = response.data;
          // this.data2 =response.data;
          console.log(response);
        })
        .catch(function (response) {
          alert("请求失败");
           console.log(response);
        });
    },
    posts: function (i) {
      // 定义传参值
      const params = new URLSearchParams();
      params.append('serverip', this.value);
      params.append('netaddress',this.input);
      axios
        .post("http://127.0.0.1:8092/extnetwork", params)
        .then((response) => {
          console.log(response)//成功
        })
        .catch(function (response) {
          alert("请求失败");
           console.log(response);
        });
    // this.$router.back()
    },
   
  },
};
</script>


